import React from 'react';
import { Route, Routes } from 'react-router'
import { MailIcon, MarkGithubIcon } from '@primer/octicons-react'
import { Heading, IconButton } from '@primer/react'
import About from './pages/About'
import Nav from './pages/Nav'
import Projects from './pages/Projects'
import Resume from './pages/Resume'
import './App.css'
import './site.scss';
import linkedinlogo from './assets/InBug-Black.png';

export default function App() {
  return (
    <>
      <Header />
      <Nav />

      <Routes>
        <Route path="/" element={<About />} />
        <Route path='/resume' element={<Resume />} />
        <Route path="/projects" element={<Projects />} />
      </Routes>

      <Footer />
    </>
  )

  function Header(): React.ReactElement {
    return (
      <div className="header d-flex flex-column text-center flex-items-center mb-5">
        <Heading>Maya Messinger</Heading>
        <Heading as="h2" className="h2">Senior Software Engineer</Heading>
      </div>
    );
  }

  function Footer(): React.ReactElement {
    return (
      <footer>
        <a href="https://www.linkedin.com/in/mayamessinger/"><IconButton icon={LinkedInIcon} aria-label="LinkedIn profile" variant="invisible" /></a>
        <a href="https://github.com/mayamessinger"><IconButton icon={MarkGithubIcon} aria-label="GitHub profile" variant="invisible" /></a>
        <a href="mailto:maya.messinger@gmail.com"><IconButton icon={MailIcon} aria-label="email address" variant="invisible" /></a>
      </footer>
    );
  }

  function LinkedInIcon() {
    return <img src={linkedinlogo} className="linkedin-icon" alt="LinkedIn"/>;
  }
}